'use strict';

/* Filters */

angular.module('snapQsgApp.filters', [])
  .filter('interpolate', ['version', function (version) {
    return function (text) {
      return String(text).replace(/%VERSION%/mg, version);
    };
  }])
  .filter('toHex', [function() {
    return function(color) {
      if (!color) { return ''; } // Early exit if nothing here.
      var rgb = color.replace(/[^\d,]/g, '').split(','), hex = '#';
      for (var j = 0; j < rgb.length; j++) {
        hex += ('0' + parseInt(rgb[j], 10).toString(16)).substr(-2, 2);
      }
      return hex;
    };
  }])
  .filter('toPaddedRgb', [function() {
    return function(color) {
      if (!color) { return ''; } // Early exit if nothing here.
      var rgb = color.replace(/[^\d,]/g, '').split(','), result = [];
      for (var j = 0; j < rgb.length; j++) {
        result.push(('0' + parseInt(rgb[j], 10).toString(10)).substr(-3, 3));
      }
      return 'rgb(' + result.join(', ') + ')';
    };
  }])
  .filter('toRgb', [function() {
    return function(color) {
      if (!color) { return ''; } // Early exit if nothing here.
      var rgb = color.replace(/[^\d,]/g, '').split(','), result = [];
      for (var j = 0; j < rgb.length; j++) {
        result.push(('0' + parseInt(rgb[j], 10).toString(10)).substr(-3, 3));
      }
      return result.join(', ');
    };
  }])
;
