// Declare app level module which depends on filters, and services
angular.module('snapQsgApp', [
    'snapQsgApp.filters',
    'snapQsgApp.services',
    'snapQsgApp.directives',
    'snapQsgApp.controllers',
    'snapQsgApp.string-utils',
    'ngRoute',
    'mm.foundation',
    'ngSanitize',
    'ngAnimate',
    'html2js'
  ])
  .config(['$routeProvider', function ( $routeProvider ) {
    //noinspection JSCheckFunctionSignatures
    $routeProvider.when('/', {templateUrl: 'partials/home.html', controller: 'HomeCtrl'});
    $routeProvider.when('/style/:section?', {templateUrl: 'style/style.html', controller: 'PageCtrl', reloadOnSearch: false});
    $routeProvider.when('/components/:component?', {templateUrl: 'components/comps.html', controller: 'PageCtrl', reloadOnSearch: false});
    $routeProvider.when('/documents/:document?', {templateUrl: 'documents/docs.html', controller: 'PageCtrl', reloadOnSearch: false});
    $routeProvider.otherwise({redirectTo: '/'});
  }])

  .run(function ( $rootScope, $anchorScroll ) {
    $rootScope.$on('$viewContentLoaded', function () {
      $(document).foundation();
    });
    var SnapUI = document.SnapUI || {};

    $rootScope.hasBuiltInDatePicker = Modernizr && Modernizr.inputtypes.date && (typeof SnapUI.nativeDatePicker === 'undefined' ? true : SnapUI.nativeDatePicker);

    // This code allows the accordion to work inside a widget. If the href starts
    // with '#id', angular won't try to handle the route. So the widget can use the Url.
    $rootScope.$on('$locationChangeStart', function ( e, newloc ) {
      if (/^.*#\/id[^#]*$/.test(newloc)) {
        e.preventDefault();
      } else {
        $anchorScroll(newloc);
      }
    });
  });