'use strict';
/* global JSON */
/* global toastr */

/* Controllers */
angular.module('snapQsgApp.controllers', [])
  .config(['$httpProvider', '$anchorScrollProvider', function ( $httpProvider, $autoScrollProvider ) {
    $autoScrollProvider.disableAutoScrolling(); //angular 1.2.X sucks at this, do it ourselves :>(
    $httpProvider.interceptors.push('TrackOutstandingHttpRequest');
  }])
  .factory('TrackOutstandingHttpRequest', ['$q', function ( $q ) {
    var
      waiters = [],
      reqCount = 0
      ;

    function onRequest() {
      reqCount++;
    }

    function onComplete() {
      reqCount--;
      if (reqCount === 0) {
        var notifyList = waiters;
        waiters = [];
        notifyList.forEach(function ( d ) {
          d.resolve();
        });
      }
    }

    return {
      request: function ( config ) {
        onRequest();
        return config;
      },
      requestError: function ( rej ) {
        onComplete();
        return $q.reject(rej);
      },
      response: function ( resp ) {
        onComplete();
        return resp;
      },
      responseError: function ( rej ) {
        onComplete();
        return $q.reject(rej);
      },
      waitForOutstandingRequest: function () {
        var d = $q.defer();
        if (reqCount !== 0) {
          waiters.push(d);
        } else {
          d.resolve();
        }
        return d.promise;
      }
    };
  }])

  .controller('HomeCtrl', ['$scope', '$routeParams', function () {
    return undefined;
  }])

  .controller('PageCtrl', ['$scope', '$routeParams', '$timeout', 'TrackOutstandingHttpRequest', '$location', function ( $scope, $routeParams, $timeout, TrackOutstandingHttpRequest, $location ) {
    $scope.loadFoundation = function () {
      $(document).foundation();
      $(document).foundation('joyride', 'start');
    };

    $scope.fonts = [
      {name: 'Open Sans Light', family: 'Open Sans', type: '', weight: '200 / light', style: 'normal', 'class': 'light', spacer: false},
      {name: 'Open Sans Regular', family: 'Open Sans', type: '', weight: '400 / normal', style: 'normal', 'class': 'regular', spacer: false},
      {name: 'Open Sans Semibold', family: 'Open Sans', type: '', weight: '500 / semibold', style: 'normal', 'class': 'semibold', spacer: false},
      {name: 'Open Sans Bold', family: 'Open Sans', type: '', weight: '700 / bold', style: 'normal', 'class': 'bold', spacer: false},
      {name: 'Open Sans Extrabold', family: 'Open Sans', type: '', weight: '900 / extrabold', style: 'normal', 'class': 'extrabold', spacer: false},
      {name: 'Open Sans Light Italic', family: 'Open Sans', type: '', weight: '200 / light', style: 'italic', 'class': 'light italic', spacer: true},
      {name: 'Open Sans Regular Italic', family: 'Open Sans', type: '', weight: '400 / normal', style: 'italic', 'class': 'italic', spacer: false},
      {name: 'Open Sans Semibold Italic', family: 'Open Sans', type: '', weight: '500 / semibold', style: 'italic', 'class': 'semibold italic', spacer: false},
      {name: 'Open Sans Bold Italic', family: 'Open Sans', type: '', weight: '700 / bold', style: 'italic', 'class': 'bold italic', spacer: false},
      {name: 'Open Sans Extrabold Italic', family: 'Open Sans', type: '', weight: '900 / extrabold', style: 'italic', 'class': 'extrabold italic', spacer: false},
      {name: 'Open Sans Condensed', family: 'Open Sans Condensed', type: '', weight: '200 / light', style: 'normal', 'class': 'condensed', spacer: true},
      {name: 'Open Sans Condensed Bold', family: 'Open Sans Condensed', type: '', weight: '700 / bold', style: 'normal', 'class': 'condensed bold', spacer: false},
      {name: 'Open Sans Condensed Italic', family: 'Open Sans Condensed', type: '', weight: '200 / light', style: 'italic', 'class': 'condensed italic', spacer: false},
      {name: 'Open Sans Condensed Bold Italic', family: 'Open Sans Condensed', type: '', weight: '700 / bold', style: 'italic', 'class': 'condensed bold italic', spacer: false},
      {name: 'Georgia Regular', family: 'Georgia', type: '(serif)', weight: '400 / normal', style: 'normal', 'class': 'serif', spacer: true},
      {name: 'Georgia Bold', family: 'Georgia', type: '(serif)', weight: '700 / bold', style: 'normal', 'class': 'serif bold', spacer: false},
      {name: 'Georgia Regular Italic', family: 'Georgia', type: '(serif)', weight: '400 / normal', style: 'italic', 'class': 'serif italic', spacer: false},
      {name: 'Georgia Bold Italic', family: 'Georgia', type: '(serif)', weight: '700 / bold', style: 'italic', 'class': 'serif bold italic', spacer: false},
      {name: 'Consolas', family: 'Consolas', type: '(monospace)', weight: '400 / normal', style: 'normal', 'class': 'monospace', spacer: true},
      {name: 'Consolas Bold', family: 'Consolas', type: '(monospace)', weight: '700 / bold', style: 'normal', 'class': 'monospace bold', spacer: false}
    ];

    $scope.grayColors = [
      {n:1, h:'#FAFAFA'},
      {n:2, h:'#EDEDED'},
      {n:3, h:'#E0E0E0'},
      {n:4, h:'#CCCCCC'},
      {n:5, h:'#BFBFBF'},
      {n:6, h:'#B3B3B3'},
      {n:7, h:'#999999'},
      {n:8, h:'#808080'},
      {n:9, h:'#595959'}
    ];

    $scope.$on('$includeContentLoaded', function ( e ) { //jshint ignore:line
      $timeout(function () {
        TrackOutstandingHttpRequest.waitForOutstandingRequest().then(function () {
          $scope.jumpTo($location.hash());
        });
      });
    });

    $scope.jumpTo = function ( jumpTo ) {
      if (jumpTo === '') {
        $('html,body').scrollTop(0);
      }
      else {
        var el = document.getElementById(jumpTo);
        if (!el) {
          el = document.getElementsByName(jumpTo)[0];
        }

        if (el) {
          $('html,body').scrollTop($(el).offset().top - ($('#top').height() + 2));
        }
      }
    };

    $(function () {
      $(document).on('click', function ( e ) {
        var href = e.target && e.target.href;
        if (href && ('' + href) === $location.absUrl()) {
          e.preventDefault();
          $timeout(function () {
            $scope.jumpTo($location.hash());
          });
        }
      });
    });

    $scope.$watch(function () {
      return $location.hash();
    }, function ( newVal, oldVal ) {
      if (newVal === oldVal && oldVal === '') {
        return;
      } //don't jump on initial load
      $scope.$evalAsync(function () {
        $scope.jumpTo($location.hash());
      });
    });

  }])
  .controller('DataGridCtrl', ['$scope', 'CodeSampleFactory', function ( $scope, CodeSampleFactory ) {
    $scope.component = 'DataGridCtrl';
    //class attribute names are defined by these strings (i.e. it will sort to borders, nostriping, and fullwidth)
    $scope.classes = CodeSampleFactory.getClasses([
      {className: 'font-size', classValues: ['font-tiny', 'font-small', 'font-large', 'font-xlarge']},
      'no-striping',
      'borders',
      'fullwidth',
      'alert'
    ]);
    $scope.getActiveClasses = function () {
      return CodeSampleFactory.getActiveClasses($scope.classes);
    };
    $scope.resetClasses = function () {
      CodeSampleFactory.resetClasses($scope.classes);
    };
  }])
  .controller('LinkListCtrl', ['$scope', 'CodeSampleFactory', function ( $scope, CodeSampleFactory ) {
    $scope.component = 'LinkListCtrl';
    $scope.classes = CodeSampleFactory.getClasses(['compact', 'light', 'flush-left', 'inherit-bg']);
    $scope.getActiveClasses = function () {
      return CodeSampleFactory.getActiveClasses($scope.classes);
    };
    $scope.resetClasses = function () {
      CodeSampleFactory.resetClasses($scope.classes);
    };
  }])
  .controller('CompoundButtonCtrl', ['$scope', 'CodeSampleFactory', function ( $scope, CodeSampleFactory ) {
    $scope.component = 'CompoundButtonCtrl';
    //class attribute names are also defined by the className attribute (i.e. it will sort to center, dark, and size)
    $scope.classes = CodeSampleFactory.getClasses([
      {className: 'size', classValues: ['small', 'medium', 'large', 'xlarge']},
      'dark',
      'center'
    ]);
    $scope.getActiveClasses = function () {
      return CodeSampleFactory.getActiveClasses($scope.classes);
    };
    $scope.resetClasses = function () {
      CodeSampleFactory.resetClasses($scope.classes);
    };
  }])
  .controller('IconPanelCtrl', ['$scope', 'CodeSampleFactory', function ( $scope, CodeSampleFactory ) {
    $scope.component = 'IconPanelCtrl';
    $scope.classes = CodeSampleFactory.getClasses([
      {className: 'size', classValues: ['small', 'medium', 'large']},
      'light',
      'inherit-bg',
      {className: 'columnNumber', classValues: ['one', 'two', 'three', 'four', 'five']}
    ]);
    $scope.getActiveClasses = function () {
      return CodeSampleFactory.getActiveClasses($scope.classes);
    };
    $scope.resetClasses = function () {
      CodeSampleFactory.resetClasses($scope.classes);
    };
  }])
  .controller('FieldSetCtrl', ['$scope', 'CodeSampleFactory', function ( $scope, CodeSampleFactory ) {
    var inlineValues = [];
    for (var i = 1; i < 12; i++) {
      inlineValues.push('inline-' + i);
    }
    $scope.component = 'FieldSetCtrl';
    $scope.classes = CodeSampleFactory.getClasses([
      {className: 'align', classValues: ['labels-left', 'labels-right']},
      'no-border',
      {className: 'columnNumber', classValues: inlineValues}
    ]);
    $scope.getActiveClasses = function () {
      return CodeSampleFactory.getActiveClasses($scope.classes);
    };
    $scope.resetClasses = function () {
      CodeSampleFactory.resetClasses($scope.classes);
    };
  }])
  .controller('SearchBoxCtrl', ['$scope', 'CodeSampleFactory', function ( $scope, CodeSampleFactory ) {
    $scope.component = 'SearchBoxCtrl';
    $scope.classes = CodeSampleFactory.getClasses([
      {className: 'color', classValues: ['inner', 'split']}
    ]);
    $scope.getActiveClasses = function () {
      return CodeSampleFactory.getActiveClasses($scope.classes);
    };
    $scope.resetClasses = function () {
      CodeSampleFactory.resetClasses($scope.classes);
    };
  }])
  .controller('ToastrCtrl', ['$scope', function ( $scope ) {
    $scope.showToastrOptions = function(){console.log(Object.keys($scope.toastr.options));};
    $scope.toastr = toastr;
    $scope.removeToastContainer = function(){$('#toast-container').remove();};
    $scope.alert = function(stuff){window.alert(stuff);};
    $scope.json = JSON;
    $scope.attrJSON = function ( obj ) {
      return JSON.stringify(obj).replace(/"/g, '\\\'');
    };
    $scope.optionsFormat=function(options, padding) {
      return JSON.stringify(options, null, 2).replace(/\n/g, '\n'+ (padding||''));
    };
    $scope.stripDoubleQuotes = function (s) { return s.replace(/"/g, '');};
    $scope.convertDoubleQuotes = function (s) { return s.replace(/"/g, '\'');};
  }])
  .controller('ProgressBarCtrl', ['$scope', function ( $scope ) {
    $scope.percentArray = [];
    for (var i = 0; i <= 100; i++) {
      $scope.percentArray[i] = 100 - i;
    }
  }])
  .controller('PanelCtrl', ['$scope', function ( $scope ) {
    $scope.sizeArray = [];
    $scope.sizeArray[0] = 'tiny';
    $scope.sizeArray[1] = 'xsmall';
    $scope.sizeArray[2] = 'small';
    $scope.sizeArray[3] = 'medium';
    $scope.sizeArray[4] = 'large';
    $scope.sizeArray[5] = 'xlarge';
    $scope.sizeArray[6] = 'jumbo';
  }]);
