'use strict';

angular.module('snapQsgApp.directives')
  //This directive reads scss files and pulls out marked sections for our documentation
  .directive('scssDoc', ['$http', '$templateCache', function ($http, $templateCache) {
    return {
      restrict: 'E',
      scope: {
        scss:'@', // the scss file
        section:'@', // the marked section
        componentName: '@' // used in the headings and text i.e. the {{componentName}} component.
      },
      replace: true,
      transclude: true,
      templateUrl: 'javascripts/scss-doc/scss-doc.template',
      link: function ($scope, elem, attrs) {
        // get the file, i.e. _accordion.scss
        var url = (attrs.root || '/snap/library/scss') + '/' + $scope.scss;

        // Added support for standardized label above the panel with a data-label="" attribute.
        $scope.label = attrs.label || false;

        $http.get(url, {cache:$templateCache}).success(function(data, status) {
          if (status !== 200) {
            console.dir({status:status,data:data});
            throw 'Error status (' + status + ') fetching ' + url;
          }

          $scope.src = data;
          $scope.txts = extractSection($scope.src, $scope.section);
        }).error(function() {
          $scope.txts=['File ' + $scope.scss + ' not found'];
        });

        function extractSection(source, expectedSection) {

          // White Space Normalization:
          // Replace carriage return/new lines with single new lines, and replace tabs with 2 spaces.
          source = source.replace(/\r\n/g, '\n');
          source = source.replace(/\t/g, '  ');

          /*
           sectRx tries to match lines of code surrounded by the following comment style:
           /// section.name{
           Lines of Code
           Lines of Code
           Lines of Code
           ///}

           *Note: section.name can technically be any character(s), other than a newline or {,
           but the loop which uses this expression expects to find passed in section.
           */
          var sectRx = /\n *\/\/\/ *([^{\n]+) *[{]((?:.|\n)*?)\n *\/\/\/ *[}][^\n]*/g;
          var ret = [];
          var sectionMatcher;
          while (null !== (sectionMatcher=sectRx.exec(source))) {
            var matchedSection = sectionMatcher[1].trim();
            var txt = sectionMatcher[2];
            if (('.' + matchedSection + '.').indexOf('.' + expectedSection + '.') > -1) {
              ret.push(txt);
            }
          }
          return ret; // an array with all marked sections (that match the passed-in section)
        }
      }
    };
  }]);
