'use strict';

angular.module('snapQsgApp.directives')
  //This directive is the outer parent of the codeSnippet directive
  .directive('codeSnippet', ['$parse', function ($parse) {
    return {
      restrict: 'E',
      scope: true,
      replace: true,
      transclude: true,
      templateUrl: 'javascripts/code-snippet/code-snippet.html',
      link: function ($scope, elem, attrs, controller, transcludeFn) {

        // This is a little utility function to select the entire range
        // of an element (http://stackoverflow.com/questions/6190143/javascript-set-window-selection)
        function selectElement(element) {
          if (window.getSelection) {
            var sel = window.getSelection();
            sel.removeAllRanges();
            var range = document.createRange();
            range.selectNodeContents(element);
            sel.addRange(range);
          } else if (document.selection) {
            var textRange = document.body.createTextRange();
            textRange.moveToElementText(element);
            textRange.select();
          }
        }

        // We set default Titles for the tabs. The 'View' tab shows the rendered content;
        // the Source tab shows the code
        $scope.htmlTitle = attrs.htmlTitle || 'View'; //
        $scope.srcTitle = attrs.srcTitle || 'Source';

        // Grab the original default options. Used to 'reset' options if desired.
        // NOTE: Even if attrs.options is undefined (it wasn't set as an attribute), we
        //   may still set it to an empty object in the transcludeFn if an options
        //   template was provided
        $scope.options=angular.copy($parse(attrs.options)($scope));
        $scope.originalOptions=angular.copy($scope.options);
        $scope.reset = function () {
          $scope.options = angular.copy($scope.originalOptions);
        };

        //***
        // There are two ways this can be called. Passing it 'el' (or anything
        // that is truthy) it assumes we're clicking on the thing we want
        // selected. Otherwise, we are outside the target and we have to
        // navigate the DOM to get to it. This is a one-line function, but
        // it's done in two for clarity.
        $scope.selectTabSource = function(event, el) {
          if ($scope.example.tab!=='src') return true; //jshint ignore:line
          var
            target = (el ? event.target : $(event.target).closest('.example-div').find('.tabs-content').find('code')[0])
            ;
          selectElement(target);
        };
        $scope.example = {};
        $scope.example.tab = attrs.initialTab || 'html';
        $scope.example.showOptions = attrs.showOptions !== undefined ? attrs.showOptions : true;


        // This function looks through the contents of the <code-snippet> tag (in compound-button.html, for example),
        // and grabs the elements to be transcluded into the template Options and Content areas.

        transcludeFn($scope, function (clone, scope) {
          for(var i = 0; i<clone.length;i++) {
            if (!clone[i].tagName) {
              continue;
            }

            if (clone[i].tagName.toLowerCase() === 'options-template') {
              elem[0].querySelector('.options-div').appendChild(clone[i]);
              if ($scope.options === undefined) {
                $scope.options = {};
                scope.originalOptions = {};
              }
            }
            else if (clone[i].tagName.toLowerCase() === 'example-template') {
              elem.append(clone[i]);
            }
          }
        });

      }
    };
  }])
  .directive('exampleTemplate', ['$interpolate', '$sce', '$parse', '$templateCache', '$http', 'codeSnippetAttributeHelperSvc', 'stringUtils',
    function ($interpolate, $sce, $parse, $templateCache, $http, codeSnippetAttributeHelperSvc, stringUtils) {
    return {
      restrict: 'E',
      scope: false,
      replace: true,
      compile: function(elem) {
        //get the example text, and then remove it so angular doesn't try to compile it
        var srcTemplate = elem.html();
        var srcTemplateFn = $interpolate(srcTemplate);
        elem.html('');

        return function($scope, elem, attrs){

          var stripLeading = false;
          if (attrs.stripLeading && $parse(attrs.stripLeading)) {
            stripLeading = true;
          }


          //a book
          if(attrs.template) {
            $http.get(attrs.template, {cache: $templateCache}).success(function (template) {
              srcTemplate = template;
              srcTemplateFn = $interpolate(template);
              $scope.$emit('update-template');
            }).error(function () {
              console.error('no file, ' + attrs.template + ' exists');
            });
          }
          //rebuild the src anytime the options change
          $scope.$watch('options', function () {
            $scope.$emit('update-template');
          }, true);
          //whenever the options change or the example template finishes loading
          $scope.$on('update-template', function(){
            //if useWhen is false, we are not the right template
            if (attrs.useWhen && !$parse(attrs.useWhen)($scope)) { return; }

            //rebuild the src using the new options, and the $interpolate function we built earlier
            //  The sce call is needed to get ng-bind-html to trust us.
            var src = srcTemplateFn({
              options:$scope.options,
              $scope:$scope,
              classList: codeSnippetAttributeHelperSvc.classList,
              classAttr:codeSnippetAttributeHelperSvc.classAttr,
              strip: codeSnippetAttributeHelperSvc.strip,
              optsToClasses:codeSnippetAttributeHelperSvc.optsToClasses,
              mapToArray:codeSnippetAttributeHelperSvc.mapToArray,
              combineMaps:codeSnippetAttributeHelperSvc.combineMaps
            });
            //If you want to remove an empty attribute (i.e. class="") from your example-template, prefix it with data-cs-opt-
            src = src.replace(/ data-cs-opt-[^=]+=""/g, '').replace(/data-cs-opt-/g, '');
            if (stripLeading) {
              src = stringUtils.normalizeLeadingSpace(src);
            }
            $scope.src = $sce.trustAsHtml(src);
            $scope.hasForm = /<form[ >]/.test(src);
          });
        };
      }
    };
  }])
  .service('codeSnippetAttributeHelperSvc', function(){

    // this will be returned, my is a stand in for this. Boo yah.
    var my = {

      //helper functions the srcTemplate can use to convert maps/arrays to class list etc.
      mapToArray: function(obj) {
        var array = [];
        Object.keys(obj).forEach(function(key) {
          if (obj[key]) {
            array.push(key);
          }
        });
        return array;
      },
      combineMaps: function(/*any number of truth maps*/) {
        return angular.extend.bind(angular, {}).apply(angular, arguments);
      },

      optsToClasses: function(obj) {
        var classes = [];
        Object.keys(obj).forEach(function(key){
          if (angular.isString(obj[key])){
            classes.push(obj[key]);
          } else if (obj[key]) {
            classes.push(key);
          }
        });
        return classes;
      },

      strip: function(obj /*, key...*/) {
        var cpy = angular.copy(obj);
        for(var i = 1; i < arguments.length; i++) {
          cpy[arguments[i]] = undefined;
        }
        return cpy;
      },

      classList: function(arrayOrClassOrString /*, ...*/) { // jshint ignore:line
        var args = Array.prototype.slice.call(arguments);
        var array = [];
        args.forEach(function(arg){
          if (angular.isArray(arg)) {
            array = array.concat(arg);
          } else if (angular.isString(arg)) {
            array.push(arg);
          } else if (arg !== undefined && arg !== null) {
            array = array.concat(my.optsToClasses(arg));
          }

        });
        return array.join(' ').replace(/ +/, ' ').trim();
      },

      classAttr: function(arrayOrClassOrString /*, ...*/) { // jshint ignore:line
        var classes = my.classList.apply(this, arguments);
        return classes.length > 0 ? ' class="' + classes + '"' : '';
      }
    };

    //told ya.
    return my;
  });
