'use strict';

angular.module('snapQsgApp.services', []).
  factory('CodeSampleFactory', [function () {
    return {
      getClasses: function (componentClasses) {
        var classes = {};
        componentClasses.forEach(function(klass){
          if (typeof klass === 'string') {
            classes[klass.replace('-', '')] = {
              className: klass,
              active: false
            };
          }
          else if (klass.className) {
            classes[klass.className.replace('-', '')] = {
              className: klass.className,
              selectedClass: '',
              classValues: klass.classValues
            };
          }
        });
        return classes;
      },
      getActiveClasses: function (classes) {
        var activeClasses = '';
        classes.forEach(function(klass){
          if (klass.active) {
            activeClasses += (klass.className + ' ');
          }
          if (klass.selectedClass) {
            activeClasses += (klass.selectedClass + ' ');
          }
        });
        return activeClasses;
      },
      resetClasses: function (classes) {
        classes.forEach(function(klass){
          klass.active = false;
          klass.selectedClass = '';
        });
      }
    };
  }]);
