'use strict';

/* Directives */


angular.module('snapQsgApp.directives', [])
/**
 * Service: 'PromiseCache'
 *
 * Simple service used by the 'codeExample' and 'codeExampleId' to communicate.
 * Makes sure that the order-of-operations is not significant in the HTML.
 *
 */
  .service('PromiseCache', ['$q', function ($q){
    var idMap = {};
    this.promise = function(id) {
      if (!idMap[id]) { idMap[id] = $q.defer(); }
      return idMap[id].promise;
    };

    this.resolve = function(id, result) {
      if (!idMap[id]) { idMap[id] = $q.defer(); }
      return idMap[id].resolve(result);
    };
  }])
  .directive('slurpExample', ['$http', '$templateCache', '$timeout', function ($http, $templateCache, $timeout) {
    return {
      restrict: 'E',
      scope: {},
      replace: true,
      template:'<div class="slurp-example"><div data-ng-if="label" class="label secondary">{{label}}</div><pre class="panel"><code id="{{guid}}"></code></pre></div>',
      link: function (scope, elem, attrs) {
        var
          url = attrs.src || null,
          guid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            var r = Math.random()*16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8); //jshint ignore:line
            return v.toString(16);
          })
        ;
        scope.guid = guid;
        if (attrs.label) {
          scope.label = attrs.label;
        }
        if (url) {
          $http.get(url, {cache:$templateCache}).success(function(data, status) {
            if (status !== 200) {
              console.dir({status:status,data:data});
              throw 'Error status (' + status + ') fetching ' + url;
            }
            var
              src = data,
              type = attrs.type || 'html'
            ;
            if (type === 'html') {
              src = '\r\n' + src.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;') + '\r\n';
            }
            else {
              src = 'Unsupported file type "' + type + '"';
            }
            $timeout(function(){
              $('#' + guid).empty().append(src);
            });
          }).error(function() {
            $timeout(function() {
              $('#' + guid).replaceWith('<h1>Woops!</h1><p>Load of URL failed: </p><pre>'+ url +'</pre>');
            });
          });
        }
        else {
          $timeout(function(){
            $('#' + guid).replaceWith('<h1>Woops!</h1><pre>No "path" attribute available.</pre>');
          });
        }
      }
    };
  }])
/**
 * Directives: codeExample and codeExampleId
 *
 * Typical Usage:
 *
 *    <data-code-example for="reveal-custom-01"></data-code-example> <!-- show the source -->
 *    <div data-code-example-id="reveal-custom-01"><!-- cache the code and inject it -->
 *      <div id="customModal" class="reveal-modal custom-reveal-class" data-reveal>
 *        <h2>Custom Class</h2>
 *        <p>This is an example of customizing the reveal modal style.</p>
 *        <a class="close-reveal-modal">&#215;</a>
 *      </div>
 *    </div>
 *
 * Source-only Usage:
 *
 *     <data-code-example>
 *       <div class="reveal-modal-bg" style="display: none"></div>
 *     </data-code-example>
 *
 * Text-only Usage:
 *
 *     <data-code-example data-as-text="true">
 *       $('#here-it-is').html('Hi!');
 *     </data-code-example>
 *
 * Description:
 *
 * Together, the next two directives implement a technique for injecting both
 * a working example of code and showing its source. With the 'for' or
 * 'data-for' attribute, codeExample will wait until it can find an object
 * with a matching 'data-code-example-id' and then inject the HTML-escaped
 * source of the code inside of the matching object (see "Typical Usage"
 * above).
 *
 * Alternatively, without a 'data-for' or 'for' attribute, codeExample simply
 * takes any HTML found inside of it and displays its HTML-escaped source
 * (see "Source-only Usage" above).
 *
 * To put non-HTML examples, the text is simply put inline inside of the standard
 * code box (see "Text-only Usage" above).
 *
 */
  .directive('codeExample', ['PromiseCache', function (PromiseCache){
    return {
      restrict: 'E',
      replace: true,
      scope: true,
      template:'<div><div data-ng-if="label" class="label secondary">{{label}}</div></div>',
      transclude: true,
      link: function($scope, elem, attrs, controllers, transcludeFn) {
        /**
         * This actually splits and trims the front-end of the HTML that it
         * finds. Basically, it's normalizing the code to the left-hand side
         * of the page. This is so the code that is displayed can be indented
         * logically in the source HTML file.
         *
         * @param html
         * @returns {string}
         */
        function sourceFromHtml(html) {
          //newline normalization
          html = ('' + html).replace(/\r\n/g, '\n').replace(/\r/g, '\n');

          var leadingSpace = /^(\s*)[^\s]/.exec(html)[1];
          leadingSpace = /[^\n]*$/.exec(leadingSpace)[0];
          html=html.trim();
          var lines = html.split(/\n/);
          lines = lines.map(function(line){
            if (line.indexOf(leadingSpace) === 0) {
              line = line.substring(leadingSpace.length);
            }
            return line;
          });

          return '\r\n' + lines.join('\r\n')
              .replace(/&/g, '&amp;')
              .replace(/</g, '&lt;')
              .replace(/>/g, '&gt;') + '\r\n\r\n';
        }

        function setSource(html) {
          if ($scope.view === 'src') {
            elem.append($('<pre class="panel">').append($('<code>').html(sourceFromHtml(html))));
          } else if ($scope.view === 'rendered') {
            elem.append($('<div>').html(html));
          }
        }

        $scope.view = 'src';
        if (attrs.view) {
          $scope.view = attrs.view;
        }

        if (attrs.label) {
          $scope.label = attrs.label;
        }
        /**
         * Implement "Source-only Usage" getting the transcluded source.
         */
        if (!attrs['for']) {
          transcludeFn($scope, function(clone){
///            console.log({clone:clone});
            var src = '';
            /**
             * Implement "Text-only Usage" where the content is somewhere else.
             */
            if (attrs.asText) {
              for (var i = 0; i < clone.length; i++) {
                src += clone[i].innerHTML;
              }
            }
            else {
              /**
               * Use jQuery to put all the clone nodes inside of a single DIV and
               * then use the html() method to get the entire source from that DIV.
               *
               * @type {*|jQuery}
               *
               * If you are including a tag that will load an external file or something,
               * make sure you "escape" it by putting 'x-' in front of it. This is useful
               * when you want to include a full HTML file where LINK, HTML, BODY, HEAD,
               * etc., tags would cause havoc. If it is a self-closing tag like LINK or
               * IMG, go ahead and close it like this:
               *
               *  <x-link rel="stylesheet" type="text/css" href="snap/snap.min.css"></x-link>
               *
               *  And this will clean up the trailing closing items for those.
               *
               */
              src = $('<div>').append(clone.clone()).html();
              if (attrs.removeClass) {
                var classes = ('' + attrs.removeClass).split(/\s+/);
                classes.forEach(function(klass) {
                  src = src.split(' ' + klass).join('');
                  src = src.split(klass + ' ').join('');
                  src = src.split(' class="' + klass + '"').join('');
                });
              }
              src = src.replace(/(<\/?)x-/gi, '$1').replace(/<\/(img|link)>/gi,'');
            }
            /**
             * Use jQuery to set the contents of the <code> tag in the template.
             */
            setSource(src);
          });
        }
        /**
         * Implements "Typical Usage"
         */
        PromiseCache.promise(attrs['for']).then(function (html) {
          setSource(html);
        });
      }
    };
  }])
/**
 * Name: 'codeExampleId'
 *
 * This directive lets the user identify a block of code that can be
 * displayed by the 'codeExample' directive. It simply adds the HTML
 * inside the object where it is used into a cache so it's available
 * to 'codeExample'. It does not modify the code in any way so it
 * should still work as normal HTML inside the document that uses it.
 * See the comments above for 'codeExample' for more information.
 */
  .directive('codeExampleId', ['PromiseCache', function (PromiseCache){
    return {
      restrict: 'A',
      link: function ($scope, elem, attrs) {
        var html = elem.html();
        // Support removing classes that Angular may inject we don't want to
        //  show the users.
        if (attrs.removeClass) {
          var classes = ('' + attrs.removeClass).split(/\s+/);
          classes.forEach(function(klass) {
            html = html.split(' ' + klass).join('');
            html = html.split(klass + ' ').join('');
          });
        }
        PromiseCache.resolve(attrs.codeExampleId, html);
        if (attrs.label) {
          angular.element(elem).prepend('<div class="label secondary">' + attrs.label + '</div> <br>');
        }
        if (attrs.viewOnly) {
          elem.remove();
        }
      }
    };
  }])
  .directive('evaluateJavascript', [function () {
    return {
      restrict: 'E',
      scope: false,
      replace: true,
      compile: function (elem) {
        //get the example text, and then remove it so angular doesn't try to compile it
        var js = elem.html();

        (window.execScript || eval)(js); // jshint ignore:line
        elem.html('');
      }
    };
  }])
  .directive('snapIconList', ['$http', '$anchorScroll', '$templateCache', function ($http, $anchorScroll, $templateCache) {
    return {
      restrict: 'E',
      scope: {
        categoryPrefix: '@?',
        categoryPrefixes: '@?',
        category: '@?',
        categories: '@?',
        iconFile: '@'
      },
      replace: true,
      templateUrl: 'partials/icon-list-template.html',
      link: function(scope){
        // Categories is a static list for grouping, where each category matches a tag associated with various icons within the json icons file.
        $http.get(scope.iconFile, {cache:$templateCache}).then(
          function(icons){
            scope.categoryPrefixes = ((scope.categoryPrefix ? scope.categoryPrefix : '') + ',' +
              (scope.categoryPrefixes ? scope.categoryPrefixes : '')).split(',');

            scope.categories = ((scope.category ? scope.category : '') + ',' +
              (scope.categories ? scope.categories : '')).split(',');

            var jsonIcons = icons.data.icons;
            scope.list = {};

            scope.expand = {all: true};

            scope.expandAll = function(expand) {
              Object.keys(scope.list).forEach(function(key){
                scope.expand[key] = expand;
              });
              scope.expand.all = expand;
            };

            var categorize = function(list, tag, itemName, isPrefix){
              isPrefix = !!isPrefix;
              var miscellaneousFlag = true;

              list.forEach(function(item){
                item = item.toString().trim();
                if(item !== '' && ((!isPrefix && tag === item) || (isPrefix && tag !== item && tag.indexOf(item) === 0))){
                  if(isPrefix) {
                    tag = tag.substring(item.length);
                  }
                  if(!scope.list[tag]){
                    scope.list[tag] = [];
                  }
                  if(scope.list[tag].indexOf(itemName) === -1) {
                    scope.list[tag].push(itemName);
                  }
                  miscellaneousFlag = false;
                }
              });
              return miscellaneousFlag;
            };

            jsonIcons.forEach(function(jsonIcon){
              var icon = jsonIcon.icon;
              var name = jsonIcon.properties.name;
              var miscellaneousFlag = true;
              icon.tags.forEach(function(tag) {
                tag = tag.toString().trim();
                miscellaneousFlag = categorize(scope.categories, tag, name) && miscellaneousFlag;
                miscellaneousFlag = categorize(scope.categoryPrefixes, tag, name, true) && miscellaneousFlag;
              });
              if(miscellaneousFlag){
                if(!scope.list.miscellaneous){
                  scope.list.miscellaneous = [];
                }
                if(scope.list.miscellaneous.indexOf(name) === -1) {
                  scope.list.miscellaneous.push(name);
                }
              }
            });

            Object.keys(scope.list).forEach(function(listCategory){
              scope.list[listCategory].sort();
            });

            scope.expandAll(scope.expand.all);
          },
          function(data){
            console.error('An error occurred and contains the following data:');
            console.dir(data);
          }
        );
      }
    };
  }])

  .directive('input', function ($timeout) {
    return {
      restrict: 'E',
      link: function (scope, elem) {
        if ((elem.attr('type') === 'date') && (elem.prop('type') === 'text')) {
          $timeout(f_tcalInit); //jshint ignore:line
        }
      }
    };
  })
//Copied from
//http://stackoverflow.com/questions/15718203/dealing-with-transformed-data-in-angularjs
  .directive('dateFormat', function ( $filter ) {
    var dateFilter = $filter('date');
    return {
      require: 'ngModel',
      link: function ( scope, element, attr, ngModelCtrl ) {
        ngModelCtrl.$formatters.unshift(function ( value ) {
          return dateFilter(value, 'MM/dd/yyyy');
        });
        ngModelCtrl.$parsers.push(function ( value ) {
          //might be a better way googling around momentjs.com/timezone seems like the best
          //  js date library aval (most have issues with timezones, even
          //  some that claim to handle it).
          var regex = /(\d{1,2})[-\/](\d{1,2})[-\/](\d{4})/.exec(value);
          if (regex && regex.length) {
            return new Date(regex[3], regex[1] - 1, regex[2]);
          } else {
            return null;
          }
        });
      }
    };
  }).directive('cssColor', ['$window', function($window) {
    return {
      restrict: 'A',
      scope: {theStyle:'=cssColor'},
      link: function (scope, elm) {
        var elmStyleMap = $window.getComputedStyle(elm[0], null);
        scope.theStyle = elmStyleMap;
      }
    };
  }])
  .directive('a', ['$location', function($location){
    return {
      restrict: 'E',
      scope: false,
      compile: function (elem) {
        var href = $(elem).attr('href');
        if (href && href.indexOf('##') === 0) {
          $(elem).attr('href', '#' + $location.url().replace(/#[^#]*$/,'') + href.substring(1));
        }
      }
    };
  }])
  .directive('sideNav', ['$http', '$location', '$templateCache', '$parse', function ($http, $location, $templateCache, $parse) {
    return {
      restrict: 'E',
      scope: true,
      replace: true,
      templateUrl: 'partials/side-nav-template.html',
      link: function(scope, elem, attr){
        scope.links = $parse(attr.links)(scope);
        scope.currentLinks = attr.unsorted || attr.unsorted === '' ? scope.links : scope.links.sort(function(a, b) {
          if (a.title > b.title) {
            return 1;
          }
          if (a.title < b.title) {
            return -1;
          }
          return 0;
        });

        // If there is no partialPath, use the routePath instead.
        var getPartial = function (link) {
          //Partial paths shouldn't start with a forward slash, so we strip it out.
          return link.partialPath || link.route.replace(/^\/?/, '');
        };

        // Change $location.path and the route will re-run.
        scope.setPage = function (link) {
          $location.url(link.route);
          $(window).scrollTop(0);
        };

        //find active link
        scope.currentLinks.forEach(function (link){
          if (link.route === $location.path()) {
            scope.selectedLink = link;
          }
        });
        if (!scope.selectedLink) {
          //route will rerun, replacing current page
          scope.setPage(scope.currentLinks[0]);
          return;
        }

        //check for existence of partial on the server
        var url = getPartial(scope.selectedLink);
        //templateCache allows you to load a page from cache instead of reloading it.
        $http.get(url, {cache:$templateCache}).success(function() {
          scope.content = url;
        }).error(function(){
          scope.content = 'partials/not-found.html';
        });
      }
    };
  }])
  .animation('.expando', function () {
    var isIE = SnapUI.isIE;

    var showIt = function (element, done) {
      if (isIE) { done(); return; } //IE sucks, not drawing things correctly :>(
      var target = $('.expando-target', element);
      if (target.length === 0) {
        target = $(element);
      }
      var height = target.height(); //determine the height of the element so we expand the right amount
      target.css('height', '0px');

      jQuery(target).animate({
        'height': height + 'px'
      }, 250, function() {
        target.css('height', '');
        done();
      });
      return function (cancelled) {
        //this optional function will be called when the animation
        //completes or when the animation is cancelled (the cancelled
        //flag will be set to true if cancelled.
        if (cancelled) {
          console.log('cancelled animation');
        }
      };
    };

    var hideIt = function (element, done) {
      if (isIE) { done(); return; } //IE sucks, not drawing things correctly :>(
      var target = $('.expando-target', element);
      if (target.length === 0) {
        target = $(element);
      }
      var height = target.height();
      target.css('height', height + 'px');
      jQuery(target).animate({
        'height': '0px'
      }, 250, function() {
        target.css('height', '');
        done();
      });
    };

    return {
      //'enter' works on ng-if (when element is added to the DOM)
      enter: function (element, done) {
        return showIt(element, done);
      },

      //'leave' works on ng-if
      leave: function (element, done) {
        return hideIt(element, done);
      },

      move: function(element, done){console.log('move'); done();},

      //'beforeAddClass works with ng-show/ng-hide
      beforeAddClass: function(element, className, done) {
        if (className === 'ng-hide') {
          return hideIt(element, done);
        }
      },



      //'addClass works with ng-show/ng-hidears
      addClass: function(element, className, done) {
        done();
      },

      //'beforeRemoveClass works with ng-show/ng-hide
      beforeRemoveClass: function(element, className, done) {
        done();
      },

      //'removeClass works with ng-show/ng-hide
      removeClass: function(element, className, done) {
        if (className === 'ng-hide') {
          // this fixes a bug with the slider. When hidden on page load, the slider doesn't
          // initialize properly. Re-rendering the screen (as with F12 or resize) fixes it.
          $(document).foundation();
          return showIt(element, done);
        }
      }
    };
  });
