'use strict';

angular.module('snapQsgApp.string-utils', [])
  .service('stringUtils', [function stringUtils(){
    this.normalizeNewlines = function(str) {
      return str.replace(/\r\n/g, '\n').replace(/\r/g, '\n');
    };

    this.normalizeLeadingSpace = function(str, nl) {
      nl = nl || '\n';
      str = this.normalizeNewlines(str);

      //grabs the leading space from the first non-blank line
      var leadingSpace = (/^\s*\n([^\S\n]*)[^\s]/.exec(str) || [undefined, ''])[1];
      return str.split(/\n/).map(function(line) {
        if (line.indexOf(leadingSpace) === 0) {
          line = line.substring(leadingSpace.length);
        }
        return line;
      }).join(nl);

    };

    this.escapeHtml = function(str) {
      return str.replace(/&/g, '&amp;')
        .replace(/</g, '&lt;')
        .replace(/>/g, '&gt;');
    };

  }]);